<template>
	<div id="loginPage">
		<div id="loginLogo">
			<img :src="`${$store.state.commonLogo.logo}`" alt="" />
		</div>
		<form action="" @submit.prevent="submitForm">
			<div class="input_item">
				<label for="id">이메일</label>
				<input
					placeholder="이메일을 입력해주세요"
					id="id"
					type="text"
					ref="inputId"
					v-model="userid"
				/>
				<div v-if="!isEmailValid && userid.length > 4" class="valid_check">
					올바른 이메일 형식으로 입력해주세요.
				</div>
			</div>
			<div class="input_item">
				<label for="password">비밀번호</label>
				<input
					placeholder="비밀번호를 입력해주세요"
					id="password"
					type="password"
					ref="inputPw"
					v-model="passwd"
				/>
			</div>

			<button class="login_btn">로그인</button>
		</form>
		<div v-if="isError" class="error_msg">
			로그인에 실패하였습니다.<br />
			이메일과 비밀번호를 확인해주세요.
		</div>
		<div v-if="isIdEmpty" class="error_msg">이메일을 입력해주세요.</div>
		<div v-if="isPwEmpty" class="error_msg">비밀번호를 입력해주세요.</div>
	</div>
</template>

<script>
import { userLogin } from "@/api/index";
import { validateEmail } from "@/api/validation";
export default {
	data() {
		return {
			userid: "",
			passwd: "",
			isError: false,
			isIdEmpty: false,
			isPwEmpty: false,
			isEmailValid: false,
			// arrUserInfo: [],
		};
	},
	watch: {
		userid(userid) {
			this.isEmailValid = validateEmail(userid);
		},
	},
	methods: {
		async submitForm() {
			try {
				if (this.userid == "") {
					// 이메일 입력안했을 때,
					this.isIdEmpty = true;
					this.isError = false;
					this.isPwEmpty = false;
					this.$refs.inputId.focus();
				} else if (this.userid != "" && this.passwd == "") {
					// 이메일만 입력하고 비밀번호 입력안했을 때,
					this.isIdEmpty = false;
					this.isPwEmpty = true;
					this.isError = false;
					this.$refs.inputPw.focus();
				} else {
					const userData = {
						userid: this.userid,
						passwd: this.passwd,
					};
					const { data } = await userLogin(userData);
					this.$store.commit("setAccessToken", data.AccessToken);
					this.$store.commit("setRefreshToken", data.RefreshToken);
					this.$store.commit("setUserId", userData.userid);
					localStorage.setItem("AccessToken", data.AccessToken);
					localStorage.setItem("RefreshToken", data.RefreshToken);
					localStorage.setItem("myEmail", this.userid);

					this.$router.push("/main");
				}
			} catch (error) {
				this.isIdEmpty = false;
				this.isPwEmpty = false;
				this.$refs.inputId.classList.add("error");
				this.$refs.inputPw.classList.add("error");
				this.isError = true;
				this.userid = "";
				this.passwd = "";
				this.$refs.inputId.focus();
			}
		},
	},
};
</script>

